@import url(https://fonts.googleapis.com/css?family=Playfair+Display);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
    background: #f12711;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #f5af19, #f12711);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(left, #f5af19, #f12711);
    background: linear-gradient(to right, #f5af19, #f12711); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    margin: 10px;
    border-radius: 10px;
    padding: 15px 10px;
}
.Header_headers {
    margin: 0;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}
.properSpacing {
  margin-right: 10px;
}
.Card {
    box-shadow: inset -1px  -8px 40px #d0d0d0;
    margin: 10px;
    border-radius: 10px;
    padding: 10px;
    -webkit-transition: box-shadow .3s;
    transition: box-shadow .3s;
}
.Card:hover {
    box-shadow: inset -12px -8px 40px #cecece;
}
.Card_bottom-image {
  display: block;
  max-width: 270px;
  height: auto;
  max-height: 250px;
  margin: auto;
}
.Card_bottom_detailsDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.Card_bottom_detailsDiv_status {
  border: solid thin #cecece;
  border-radius: 10px;
  background-color: #FFEB3B;
  padding: 2px 10px;
}
.statusDone {
  background-color: rgb(194, 255, 138);
}
.statusInDev {
  background-color: #FFEB3B;
}
.Card_bottom_detailsDiv_button {
  border: none;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  padding: 6px 8px;
}
.Card_bottom_detailsDiv_button:hover {
  box-shadow: 2px 2px 4px rgb(144, 144, 144);
}
.Card_bottom_linksDiv {
  display: -webkit-flex;
  display: flex;
}
.websiteLink, .githubLink {
  border: solid thin #cecece;
  border-radius: 10px;
  background-color: #ffffff;
  padding: 2px 10px;
  text-decoration: none;
  color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.icon {
  margin-right: 4px;
}
.gridContainer {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}
.slideshow-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px;
}
.slideshow-container-button {
  -webkit-align-self: center;
          align-self: center;
}
.slideshow-container-image {
  height: 400px;
  width: auto;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}
.popup {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  position: absolute;
  left: 10px;
  background-color: #fff;
  border-radius: 8px;
  right: 10px;
  bottom: 10px;
  background-color: #e4e4e4
}
.popup_header {
  background: #2193b0;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #6dd5ed, #2193b0);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #6dd5ed, #2193b0);
  background: linear-gradient(to right, #6dd5ed, #2193b0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0px 2%;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px
}
.popup_header_close { 
  height: -webkit-fit-content; 
  height: -moz-fit-content; 
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  border-radius: 100%;
  -webkit-align-self: center;
          align-self: center;
  padding: .5em .7em;
  background-color: #fff;
  cursor: pointer;
}
.popup_header_close:hover {
  box-shadow: 0 3px 6px #12677a, 0 3px 6px #12677a;
}
.popup_technologies {
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  -webkit-justify-content: center;
          justify-content: center;
  background: #fff;
}
.popup_technologies_container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 10px;
}
.popup_technologies_image {
  height: 70px;
  width: auto;
}
.popup_grid {
  display: grid;
  grid-template-columns: 30% 70%;
  grid-template-rows: auto;
}
.popup_grid_explanation {
  background: #fff;
  padding: 10px;
}
.popup_grid_explanationTxt {
  font-family: 'Ubuntu', sans-serif;
}
.popup_grid_slideshow {
  background-color: #e4e4e4;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

